import '../App.css'
import notedible from '../bilder/ejatbar.svg'
import poison from '../bilder/poison.svg'
import skull from '../bilder/skull.svg'
import lacker from '../bilder/lacker.svg'
import god from '../bilder/god.svg'
import atlig from '../bilder/atlig.svg'
import pot from '../bilder/pot.svg'

function displayEdibility (data) {
  if (data.edibilityValue === 'O Ej matsvamp') {
    return (
      <div>
        <img className='iconEdible' src={notedible} alt='√§tbarhet' />
        <span> Ej matsvamp</span>
      </div>
    )
  } else if (data.edibilityValue === '*** L√§cker matsvamp') {
    return (
      <div>
        <img className='iconEdible' src={lacker} alt='√§tbarhet' />
        <span> L√§cker matsvamp</span>
      </div>
    )
  } else if (data.edibilityValue === '** God matsvamp') {
    return (
      <div>
        <img className='iconEdible' src={god} alt='√§tbarhet' />
        <span> God matsvamp</span>
      </div>
    )
  } else if (data.edibilityValue === '* √Ñtlig') {
    return (
      <div>
        <img className='iconEdible' src={atlig} alt='√§tbarhet' />
        <span> √Ñtlig</span>
      </div>
    )
  } else if (data.edibilityValue === '‚Ä† Giftig') {
    return (
      <div>
        <img className='iconEdible' src={poison} alt='√§tbarhet' />
        <span> Giftig</span>
      </div>
    )
  } else if (data.edibilityValue === '‚Ä†‚Ä† D√∂dligt giftig') {
    return (
      <div>
        <img className='iconEdible' src={skull} alt='√§tbarhet' />
        <span> D√∂dligt giftig</span>
      </div>
    )
  } else if (data.edibilityValue === '‚Ä†‚Ä† (***) L√§cker matsvamp r√§tt behandlad') {
    return (
      <div>
        <img className='iconEdible' id='poticon' src={pot} alt='√§tbarhet' />
        <span><br /> D√∂dligt giftig <br />(L√§cker matsvamp r√§tt behandlad)</span>
      </div>
    )
  } else if (data.edibilityValue === 'O (**) God matsvamp efter avkokning') {
    return (
      <div>
        <img className='iconEdible' id='poticon' src={pot} alt='√§tbarhet' />
        <span> Ej matsvamp <br />(God matsvamp efter avkokning)</span>
      </div>
    )
  } else if (data.edibilityValue === 'O (*) √Ñtlig efter avkokning') {
    return (
      <div>
        <img className='iconEdible' id='poticon' src={pot} alt='√§tbarhet' />
        <span> Ej matsvamp<br />(√Ñtlig efter avkokning)</span>
      </div>
    )
  } else {
    return <img className='iconEdible' src='' alt='√§tbarhet' />
  }
}

export default displayEdibility
